import * as THREE from 'three';
import { RendererThree, NJSCORE } from '../../../../lib';
import { getJSONFromURL, } from 'njscore';

import { FilterGLViewDarkForDarken } from '../01_JSAPI/Tint/FilterGLViewDarkForDarken';
import { Drawing } from './Drawing';
import { DataTwitter } from './DataTwitter';

import { UI } from './UI';
export class Solution extends RendererThree {

    drawing: Drawing;
    ui: UI;

    meshAreaGon: THREE.Mesh;
    meshPolygon2: THREE.Mesh;

    pts: number[] = [];

    dataTwitter: DataTwitter;

    xOFf = -15478803.88731685;
    yOFf = -4766681.448690384;
    scale = 0.01;

    PARAM: any;

    constructor(id: string = 'main') {
        super(id)

        let paramURL = NJSCORE.BrowserUtility.getAllUrlParams();
        this.PARAM = JSON.parse(JSON.stringify(paramURL));

        // console.log(this.PARAM);


        this.drawing = new Drawing(this);
        this.dataTwitter = new DataTwitter(this);

        const light = new THREE.PointLight(0xffffff, 0.8);
        this.camera.add(light);
        this.scene.add(light);

        const canvasGL = document.createElement('canvas');
        canvasGL.style.position = 'absolute';
        canvasGL.style.pointerEvents = 'none';
        canvasGL.style.mixBlendMode = 'darken';
        canvasGL.width = this.hostDiv.clientWidth;
        canvasGL.height = this.hostDiv.clientHeight;
        this.hostDiv.appendChild(canvasGL);
        const glCtx = canvasGL.getContext('webgl');
        const d = new FilterGLViewDarkForDarken(glCtx, 0.5, 5);
        d.Render(glCtx);



        this.ui = new UI(this);
        if (this.PARAM.posCamera) {
            const pos = this.PARAM.posCamera.split(',');
            this.camera.position.set(+pos[0], +pos[1], +pos[2]);
            this.controls.enablePan = false;
            this.controls.enableRotate = false;
            this.controls.enableZoom = false;
        } else {
            this.camera.position.set(0, 100, 100);

        }
        if (this.PARAM.posTarget) {
            const pos = this.PARAM.posTarget.split(',');
            this.controls.target.set(+pos[0], +pos[1], +pos[2]);
        } 

    }
    public projectionToMercator(lon: number, lat: number) {
        const [xPro, yPro] = MercatorProjection(lon, lat);
        return [
            (xPro + this.xOFf) * this.scale,
            (yPro + this.yOFf) * this.scale * -1
        ];
    }
    public updateParam() {
        if (this.PARAM.scale) {
            this.ui.isActive_twitter = true;
            this.dataTwitter.dataScale = +this.PARAM.scale;
        }
        if (this.PARAM.colorScale) {
            this.ui.isActive_twitter = true;
            this.dataTwitter.colorScale = +this.PARAM.colorScale;
        }
        if (this.PARAM.dataLayer) {
            this.ui.isActive_twitter = true;
            this.dataTwitter.updateMesh(this.PARAM.dataLayer);
        }

    }
    public update(scene: THREE.Scene) {
        this.drawing.update();
        this.dataTwitter?.update();

        const pos = `Pos: ${this.camera.position.x}, ${this.camera.position.y}, ${this.camera.position.z}; Target: ${this.controls.target.x}, ${this.controls.target.y}, ${this.controls.target.z}`;
        if (this.ui.divCameraPos !== undefined) {
            this.ui.divCameraPos.textContent = pos;
        }
    }
}

