export const MercatorProjection = (longitude: number, latitude: number) => {

    // latitude    = 41.145556; // ()
    // longitude   = -73.995;   // ()

    const  mapWidth = 30559983.333; // 200
    const  mapHeight = 446; // 100

    // get x value
    const  x = (longitude + 180) * (mapWidth / 360);

    // convert from degrees to radians
    const  latRad = latitude * Math.PI / 180;

    // get y value
    const  mercN = Math.log(Math.tan((Math.PI / 4) + (latRad / 2)));
    const  y = (mapHeight / 2) - (mapWidth * mercN / (2 * Math.PI)) * -1;
    return [x, y];
}